.center-block{
	float:none !important;
}

.valign {
	position: absolute;
	top: 50%;
	right: 50%;
	transform: translateY(-50%) translateX(50%);
}

.panel {
    border-radius: 0px;
	border-color: $gray;
}

.loading{
	background: rgba(white,0.5);
	width:100%;
	height:100%;
	position:absolute;
	left:0px;
	top:0px;
	z-index:1000;
	.preloader-wrapper{
		position: absolute;
		top: 200px;
		left: 50%;
	}
}

&.toolbar{
	padding:10px 0;
	margin:0px; 
	.container{
	}
}

section{
	padding:10px 0;
	margin:0px !important;
}